import { actionTypes } from "@servicenow/ui-core";
import get from "lodash/get";
import isString from "lodash/isString";

const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const hasInvalidUibProps = (uibProps, properties) =>
	uibProps.some(propName => {
		const prop = get(properties, propName, "");
		return isString(prop) && prop.includes("@");
	});

const checkForInvalidUibProps = coeffects => {
	const {
		action: {
			payload: {
				options: { configurableUibProps }
			}
		},
		updateState,
		properties
	} = coeffects;

	updateState({
		hasUibConfigError: hasInvalidUibProps(configurableUibProps, properties)
	});
};

const onBootstrap = coeffects => {
	const {
		properties: { workspaceConfigId }
	} = coeffects;

	if (!workspaceConfigId) checkForInvalidUibProps(coeffects);
};

const onPropertyChanged = coeffects => {
	const {
		action: {
			payload: {
				name,
				options: { configurableUibProps }
			}
		},
		properties: { workspaceConfigId }
	} = coeffects;

	if (!workspaceConfigId && configurableUibProps.includes(name))
		checkForInvalidUibProps(coeffects);
};

export const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: onBootstrap,
	[COMPONENT_PROPERTY_CHANGED]: onPropertyChanged
};
