import "../../components/ariaLiveRegion/ariaLiveRegion";
import { ARIA_LIVE_MESSAGE, ARIA_MESSAGE_MAX } from "./constants";
import { actionTypes } from "@servicenow/ui-core";

const { COMPONENT_CONNECTED, COMPONENT_DISCONNECTED } = actionTypes;

const STATIC_ARIA_REGISTRY = new Set(); // Need access to size, behavior is setup to remove any lingering refs
let __ariaLiveReference;

const onConnect = ({ host }) => {
	if (!STATIC_ARIA_REGISTRY.size) {
		const el = document.createElement("sn-aria-live-region");
		document.body.appendChild(el);
		__ariaLiveReference = el;
	}

	if (!STATIC_ARIA_REGISTRY.has(host)) STATIC_ARIA_REGISTRY.add(host);
};

const onDisconnect = ({ host }) => {
	STATIC_ARIA_REGISTRY.delete(host);
	if (STATIC_ARIA_REGISTRY.size) return;

	__ariaLiveReference.remove();
	__ariaLiveReference = null;
};

const liveMessageEffect = ({ action: { payload } }) => {
	if (!__ariaLiveReference) return;

	const { message } = payload;
	const currentMessages = __ariaLiveReference.getProperty("messages");
	// Ensure messages only contains latest ARIA_MESSAGE_MAX messages to save us from node spam
	__ariaLiveReference.messages = [
		...currentMessages.slice(-(ARIA_MESSAGE_MAX - 1)),
		message
	];
};

export const actionHandlers = {
	[COMPONENT_CONNECTED]: onConnect,
	[COMPONENT_DISCONNECTED]: onDisconnect,
	[ARIA_LIVE_MESSAGE]: {
		effect: liveMessageEffect,
		stopPropagation: true
	}
};
