import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import styles from "./ariaLiveRegion.scss";

const view = state => {
	const {
		properties: { messages }
	} = state;
	const classes = "sr-only";

	return (
		<div className={classes} aria-live="polite" aria-relevant="additions">
			{messages.map(message => {
				return <span>{message}</span>;
			})}
		</div>
	);
};

createCustomElement("sn-aria-live-region", {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		messages: {
			default: []
		}
	},
	styles
});
